<template>
  <div>
    <table class="table mt-5">
      <thead>
        <th width="120"></th>
        <th>E-mail</th>
        <th></th>
        <th width="120"></th>
        <th width="120"></th>
      </thead>
      <tbody>
        <tr
          v-for="item in orders"
          :key="item.id"
        >
          <td>{{ item.create_at|timeStamp }}</td>
          <td>{{ item.user.email }}</td>
          <td>
            <ul style="list-style-type:none;">
              <li
                v-for="product in item.products"
                :key="product.id"
              >
                {{ product.product.title }} : {{ product.product.num }}{{ product.product.unit }}
              </li>
            </ul>
          </td>
          <td class="text-right">{{ item.total | currency }}</td>
          <td>
            <span
              v-if="item.is_paid"
              class="text-success"
            >
              
            </span>
            <span v-else></span>
          </td>
        </tr>
      </tbody>
    </table>
    <Pagination
      :pagination-prop="pagination"
      @emit-update-page="getOrders"
    />
  </div>
</template>

<script>
import Pagination from '../../../components/Pagination'; // Pagination 

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      orders: [],
      pagination: {},
    };
  },
  created() {
    this.getOrders();
  },
  methods: {
    getOrders(page = 1) {
      const apiPath = process.env.VUE_APP_APIPATH; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm = this;
      const api = `${apiPath}/api/${customPath}/orders?page=${page}`; // Api + 
      this.$http.get(api).then((response) => {
        console.log('', response);
        vm.orders = response.data.orders;
        console.log('', response.data.orders);
        vm.pagination = response.data.pagination;
      });
    },
  },
};
</script>
