                  >
                </div>
                <div class="form-group ">
                  <label for="price"></label>
                  <input
                    id="unit"
                    v-model="newTicket.percent"
                    type="unit"
                    class="form-control"
                    placeholder=""
                  >
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input
                      id="is_enabled"
                      :true-value="1"
                      :false-value="0"
                      v-model="newTicket.is_enabled"
                      class="form-check-input"
                      type="checkbox"
                    >
                    <label
                      class="form-check-label"
                      for="is_enabled"
                    >
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
