  </div>
</template>

<script>
import $ from 'jquery'; // jquery  ESLint $
import Pagination from '../../../components/Pagination'; // Pagination 

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      // isLoading:false,
      pagination: {}, // 
      newTicket: {
        percent: {
          default: 100,
          type: Number,
        },
      }, // timeStamp
      tickets: [], // 
      isNew: false, // 
    };
  },
  created() {
    this.getTickets();
  },
  methods: {
    //    
    // page 1  getProduct() pagepage1
    getTickets(page = 1) {
      const apiPath = process.env.VUE_APP_APIPATH; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm = this;
      const api = `${apiPath}/api/${customPath}/admin/coupons?page=${page}`; // Api + 
      vm.$store.dispatch('updateLoading', true);
      this.$http.get(api).then((response) => {
        console.log(response.data);
        vm.$store.dispatch('updateLoading', false);
        vm.tickets = response.data.coupons;
        vm.pagination = response.data.pagination;
      });
    },
    openModal(isNew, item) {
      if (isNew) { // isNew data return  this.isNew
        this.newTicket = {}; //  this.newTicket  modal input 
        this.isNew = true; //  isNew  true
      } else {
        // Object.assign({},item) => ES6 item  1item1
        // itemmodal this.newTicket
        // item modal input 
        // this.newTicket = Object.assign({}, item); //  { ...item }
        this.newTicket = { ...item }; // ESLint Object bread
        this.isNew = false; //  isNew  false
      }
      $('#ticketModal').modal('show');
    },
    updateTicket() {
      const apiPath = process.env.VUE_APP_APIPATH; // config/dev.env.jsAPIPATH
      const customPath = process.env.VUE_APP_CUSTOMPATH; // config/dev.env.jsCUSTOMPATH
      const vm = this;
      let httpMethod = 'post';
      let api = `${apiPath}/api/${customPath}/admin/coupon`; // api
      if (!vm.isNew) { // isNewfalse apiapi
        api = `${apiPath}/api/${customPath}/admin/coupon/${vm.newTicket.id}`; // /api/:api_path/admin/product/:id id newTicket  id
        httpMethod = 'put'; //  http put
      }
      //   apidata  {data:vm.newTicket} 
      this.$http[httpMethod](api, { data: vm.newTicket }).then((response) => {
        console.log(response.data);
        if (response.data.success) { // 
          console.log(response);
          $('#ticketModal').modal('hide'); // modal
          vm.getTickets();// 
          this.$bus.$emit('message:push', response.data.message, 'success'); // 
        } else {
          console.log(response);
          $('#ticketModal').modal('hide'); // modal
          vm.getTickets(); // 
          this.$bus.$emit('message:push', response.data.message, 'danger'); // 
        }
      });
    },
  },
};
</script>
