<template>
  <div
    id="eventAlert"
    class="modal mt-5"
    tabindex="-1"
    role="dialog"
  >
    <div
      class="modal-dialog modal-max-width"
      role="document"
    >
      <div class="modal-content ">
        <div class="modal-header bg-danger ">
          <h5 class="modal-title text-white "> {{ seconds }} </h5>
          <button
            class="close"
            type="button"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <div class="modal-body">
          <p class="h3 text-danger text-center"> <b>108!</b> </p>
        </div>
        <div class="modal-footer">
          <button
            class="btn btn-danger"
            type="button"
            data-dismiss="modal"
          >
            
          </button>
          <button
            class="btn btn-primary"
            type="button"
            @click.prevent="toEvent"
          >
            
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';

export default {
  data() {
    return {
      show: false, // mountedmodal
      seconds: 3, // 
    };
  },
  mounted() {
    const vm = this;
    vm.alertEvent();
  },
  methods: {
    alertEvent() {
      const vm = this;
      if (!vm.show) { // modal
        $('#eventAlert').modal('show');
        vm.show = true;
        vm.closeModal();
      }
    },
    closeModal() {
      const vm = this;
      if (vm.show) { // modal
        setTimeout(() => {
          $('#eventAlert').modal('hide');
          vm.show = false;
        }, 3000);
        setTimeout(() => { //  3 2 1
          vm.seconds = 2;
        }, 1000);
        setTimeout(() => {
          vm.seconds = 1;
        }, 2000);
      }
    },
    toEvent() { // modal  modaldisabled
      const vm = this;
      $('#eventAlert').modal('hide');
      vm.show = false;
      vm.$router.push('/event');
    },
  },
};
</script>
